import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import DetailScreen from  '../src/presentation/screens/DetailScreen'// './DetailScreen';

// Mock de la navegaciÃ³n
jest.mock('@react-navigation/native', () => {
  return {
    ...jest.requireActual('@react-navigation/native'),
    useNavigation: jest.fn(),
  };
});

describe('DetailScreen', () => {
  test('renders correctly', () => {
    const ship = {
      image: 'https://example.com/image.jpg',
      name: 'Ship Name',
    };

    const Stack = createStackNavigator();

    const { getByTestId } = render(
      <NavigationContainer>
        <Stack.Navigator>
          <Stack.Screen
            name="Detail"
            component={() => <DetailScreen route={{ params: { ship } }} />}
          />
        </Stack.Navigator>
      </NavigationContainer>
    );

    const imageElement = getByTestId('detail-image');
    const nameElement = getByTestId('detail-name');

    expect(imageElement.props.source.uri).toBe(ship.image);
    expect(nameElement.props.children).toBe(ship.name);
  });
});
